.main-feedback {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.feedback-header {
  align-items: center;
  background-color: #282c34;
  color: rgb(213, 221, 249);
  display: flex;
  justify-content: space-around;
  padding: 16px;
  width: 97.7vw;
}

.red-feedback {
  color: red;
}

.blue-feedback {
  color: blue;
}

.botoes-feedback {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 32px;
}

.btn-feedback {
  border: 2px solid black;
  margin: 8px;
  padding: 8px;
  width: 10vw;
}

.feedback {
  height: 300px;
  width: 250px;
}
