header > h1 {
  background-color: #ae841a;
  color: white;
  margin-bottom: 8px;
  padding: 8px;
}

td, tr, th {
  align-items: center;
  display: flex;
  height: 100px;
  justify-content: center;
  width: 500px;
}

tr:nth-child( even ) {
  background-color: rgb(213, 221, 249);
  color: #282c34;
}

tr:nth-child( odd ) {
  background-color: #282c34;
  color: rgb(213, 221, 249);
}


table {
  width: 98vw;
}

.ranking-header {
  background-color: rgb(14, 124, 123) !important;
}

.ranking-btn {
  background-color: rgb(213, 221, 249);
  color: black;
  font-size: 15px;
  margin: 16px;
  padding: 8px;
  width: 15vw;
}

.ranking-btn:hover {
  background-color: rgb(14, 124, 123);
  color: white;
  padding: 8px;
}
