* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(83, 221, 108);
  color: black;
}

input {
  background-color: rgb(213, 221, 249);
  border-radius: 8px;
  margin: 4px;
  padding: 12px;
  width: 70%;
}

button {
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
}

.efeito-vidro {
  backdrop-filter: blur(13.5px);
  background: rgba(255, 255, 255, 0.35);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 10px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.container-led {
  display: flex;
  justify-content: space-around;
}

.centraliza-raking {
  text-align: center;
}

.centraliza-raking-result {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 50px;
  justify-content: space-between;
}

.red-border {
  border: 3px solid red;
  margin-left: 8px;
  padding: 8px;
  width: 10vw;
}

.green-border {
  border: 3px solid rgb(6, 240, 15);
  margin-left: 8px;
  padding: 8px;
  width: 10vw;
}

@media ( prefers-reduced-motion : no-preference ) {

  /* .efeito-vidro {
    animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  } */
}

@keyframes shake {

  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
