.login-container {
  align-items: center;
  display: flex;
  height: 95vh;
  justify-content: center;
}

.login {
  align-items: center;
  background-color: #282c34;
  box-shadow: 0 8px 32px 0 #282c34;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 50vh;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 30vw;
}

.play-config-buttons {
  background-color: rgb(213, 221, 249);
  color: black;
  font-size: 15px;
  margin: 4px;
  padding: 8px;
  width: 15vw;
}

.play-config-buttons:active {
  background-color: rgb(14, 124, 123);
  color: white;
  margin: 4px;
  padding: 8px;
}
