.main-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 26px;
}

.game-container {
  align-content: center;
  align-items: center;
  align-self: center;
  background-color: #282c34;
  border-radius: 8px;
  color: rgb(213, 220, 249);
  display: flex;
  flex-direction: column;
  height: 50vh;
  justify-content: space-between;
  margin-top: 0;
  padding: 16px;
  text-align: center;
}

.loading-container {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.alternatives-buttons {
  border: 2px solid black;
  margin-left: 8px;
  padding: 8px;
  width: 10vw;
}

.alternatives-buttons:hover {
  border: 2px solid white;
  padding: 8px;
  width: 10vw;
}
